.gall_li .thumb a {
  display: block;
  position: relative;
  height: 160px;
  background-color: #f8f8f8;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-image: url("../img/common/no_image.png");
}
.gall_li .thumb a:after {
  content: "";
  transition: all 0.15s;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border: 6px solid #f8f8f8;
}
.gall_li .caption {
  height: 96px;
  padding-top: 15px;
}
.gall_li .caption .subject {
  color: #333333;
  padding-bottom: 8px;
  font-size: 14px;
}
.gall_li .caption .subject a {
  display: inline-block;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.gall_li .caption .date {
  color: #999999;
}
.gall_li:hover .thumb a:after {
  opacity: 1;
  border-color: #264998;
}

.faq {
  border-top: 1px solid #333333;
}
.faq__li:hover .faq__subject, .faq__li.active .faq__subject {
  background-color: #f8f8f8;
}
.faq__li:hover .faq__subject:after, .faq__li.active .faq__subject:after {
  background-color: #264998;
  color: #fff;
}
.faq__li.active .faq__subject:before {
  transform: rotate(45deg) translateY(-50%);
}
.faq__subject {
  position: relative;
  display: flex;
  padding: 18px;
  padding-left: 60px;
  align-items: center;
  border-bottom: 1px solid #e1e1e1;
  cursor: pointer;
  transition: all 0.15s;
}
.faq__subject:after {
  content: "Q";
  display: block;
  position: absolute;
  left: 25px;
  top: 10px;
  border-radius: 50%;
  text-align: center;
  line-height: 28px;
  height: 28px;
  width: 28px;
  background-color: #f1f1f1;
  color: #333333;
  font-weight: 500;
  transition: all 0.15s;
}
.faq__subject:before {
  content: "";
  display: block;
  transition: all 0.15s;
  position: absolute;
  right: 25px;
  top: 50%;
  transform: rotate(0deg) translateY(-50%);
  width: 16px;
  height: 16px;
  background: url("../img/common/faq-off.png") no-repeat 50% 50%;
}
.faq__cont {
  position: relative;
  display: none;
  padding: 38px 60px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #e1e1e1;
}
.faq__cont .txt {
  color: #666;
  font-size: 14px;
  line-height: 1.34;
}
.faq__cont:after {
  content: "A.";
  display: block;
  position: absolute;
  left: 31px;
  top: 40px;
  color: #264a98;
  font-weight: 700;
  font-size: 14px;
}

/**
 * @type		component
 * @name		dialog-popup
 * @moudle		module-a
**/
html.active-bui-dialog-popup body {
  overflow: hidden;
}

.bui-dialog-popup.module-a[data-bui-modal] {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  z-index: 60;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 24px;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0);
  transition: visibility 0.25s ease, background-color 0.25s ease;
  pointer-events: none;
}
.bui-dialog-popup.module-a[data-bui-modal] .popup-page .popup-page-body {
  visibility: hidden;
  opacity: 0;
